	  "content": "<style>.post{max-width:700px;margin:auto}</style><h1 id=features><a href=#features aria-hidden=true><span class=\"icon icon-link\"></span></a>Features</h1><p><img src=./imgs/features.svg data-image-loader=0></p><h2 id=inferred-configuration><a href=#inferred-configuration aria-hidden=true><span class=\"icon icon-link\"></span></a>Inferred Configuration</h2><p>Edam tracks the first matched configuration starting with <code>process.cwd()</code>, and lookup the parent <code>../</code> paths and so on like <code>.babelrc</code>. (<a href=https://github.com/davidtheclark/cosmiconfig>cosmiconfig</a>)</p><p>JSON file <code>.edamrc</code> supports human-like syntax like comment.(<a href=https://github.com/json5/json5>JSON5</a>)</p><p>It's awesome that edam supports <code>extends</code> field like <a href=http://www.typescriptlang.org/docs/handbook/tsconfig-json.html><code>tsconfig.json</code></a>.</p><pre><code class=\"hljs language-text\"data-query={} data-lang=text><span class=hljs-comment>// root/.edamrc</span>\n{\n  <span class=hljs-attribute>alias</span>: {\n    <span class=hljs-attribute>react</span>: <span class=hljs-string>'facebook/react'</span>\n  },\n  <span class=hljs-attribute>plugins</span>: [<span class=hljs-string>'edam-plugin-dulcet-prompt'</span>]\n}\n<span class=hljs-comment>// root/tpl/.edamrc</span>\n{\n  <span class=hljs-attribute>extends</span>: [<span class=hljs-string>'../.edamrc'</span>]\n  <span class=hljs-attribute>alias</span>: {\n    <span class=hljs-attribute>edam</span>: {\n      <span class=hljs-attribute>type</span>: <span class=hljs-string>'git'</span>,\n      <span class=hljs-attribute>url</span>: <span class=hljs-string>'imcuttle/edam'</span>,\n      <span class=hljs-attribute>config</span>: {\n        <span class=hljs-attribute>output</span>: <span class=hljs-string>\"./here\"</span>\n      }\n      <span class=hljs-comment>// The config has higher priority when source equals `edam`</span>\n      <span class=hljs-comment>// Support fields: cacheDir / output / plugins / storePrompts / pull</span>\n      <span class=hljs-comment>// version >= 2.2</span>\n    }\n  }\n}\n<span class=hljs-comment>// root/tpl/.edamrc </span>\n{\n  <span class=hljs-attribute>alias</span>: {\n    <span class=hljs-attribute>edam</span>: {\n      <span class=hljs-attribute>type</span>: <span class=hljs-string>'git'</span>,\n      <span class=hljs-attribute>url</span>: <span class=hljs-string>'imcuttle/edam'</span>,\n      <span class=hljs-attribute>config</span>: {\n        <span class=hljs-attribute>output</span>: <span class=hljs-string>\"./here\"</span>\n      }\n    },\n    <span class=hljs-attribute>react</span>: <span class=hljs-string>'facebook/react'</span>\n  },\n  <span class=hljs-attribute>plugins</span>: [<span class=hljs-string>'edam-plugin-dulcet-prompt'</span>]\n}</code></pre><h2 id=three-pull-ways><a href=#three-pull-ways aria-hidden=true><span class=\"icon icon-link\"></span></a>Three pull ways</h2><p>Supports <code>npm/git/file</code></p><h3 id=npm><a href=#npm aria-hidden=true><span class=\"icon icon-link\"></span></a><code>npm</code></h3><p>Pulls template assets from <code>npm</code> like <code>npm:edam@1.1.1</code> or shape of the follow in file.</p><pre><code class=\"hljs language-text\"data-query={} data-lang=text>{\n  <span class=hljs-attribute>source</span>: {\n    type: <span class=hljs-string>'npm'</span>,\n    url: <span class=hljs-string>'edam'</span>,\n    version: <span class=hljs-string>'latest'</span>\n  }\n}</code></pre><h3 id=git><a href=#git aria-hidden=true><span class=\"icon icon-link\"></span></a>git</h3><p><code>github:imcuttle/cuttle?checkout=master</code></p><pre><code class=\"hljs language-text\"data-query={} data-lang=text>{\n  <span class=hljs-attribute>source</span>: {\n    <span class=hljs-attribute>type</span>: <span class=hljs-string>'git'</span>,\n    <span class=hljs-attribute>url</span>: <span class=hljs-string>'https://github.com/imcuttle/edam.git'</span>,\n    <span class=hljs-comment>// branch / commit SHA / tag</span>\n    <span class=hljs-attribute>checkout</span>: <span class=hljs-string>'master'</span>\n  }\n}</code></pre><h3 id=file><a href=#file aria-hidden=true><span class=\"icon icon-link\"></span></a>file</h3><p>It's easy to understand.</p><h2 id=prompts-hold><a href=#prompts-hold aria-hidden=true><span class=\"icon icon-link\"></span></a>Prompts-hold</h2><p>Edam could save the latest user-input when <code>storePrompts</code> is on. and you can avoid the frequent input passing <code>yes</code> flag.</p><h2 id=loader><a href=#loader aria-hidden=true><span class=\"icon icon-link\"></span></a>Loader</h2><p>Please checks out <a href=/advanced/write-template>here</a>.</p><h2 id=cli-auto-completer><a href=#cli-auto-completer aria-hidden=true><span class=\"icon icon-link\"></span></a>CLI auto-completer</h2><p><a href=https://github.com/imcuttle/edam/blob/master/packages/edam-completer/Readme.md>edam-completer</a></p>",
